<template>
	<view>
		<view class="container">
			<view class="swiper_wrap">
				<view class="swiper_item" v-for="(item,index) in swiper_data" :style="{zIndex:(5-index)}"
					:data-id='index' :key="index" :animation="item.anim" @touchstart="touchStart" @touchmove="touchMove"
					@touchend="touchEnd">
					<view class="cover-img">
						<image src="../../static/image/swiper.png" mode=""></image>
						<view class="surplus flex row-end column-center">
							<view class="colorfff font32">18</view>
							<view class="font22">剩余匹配</view>
						</view>
						<view class="right-user">
							<view class="right-user-top">
								<view class="right-user-name flex area-between">
									<view class="right-user-name-text flex row-center">
										<text class="font38 colorfff">小豆子</text>
										<image src="../../static/image/VIP.png" mode="" class="vipIcon"></image>
										<!-- <image src="../../static/image/SVIP.png" mode="" class="vipIcon"></image> -->
										<view class="realPeople flex area-center font18 colorfff">
											<image src="../../static/image/smile-act.png" mode=""
												style="width: 18rpx;height: 18rpx;"></image>真人
										</view>
										<view class="realPeople flex area-center font18 colorfff">
											<image src="../../static/image/plaint-white.png" mode=""
												style="width: 10rpx;height: 20rpx;"></image>女神
										</view>
										<!-- <view class="flex area-center font18 colorfff">
													<image src="../../static/image/plaint-yellow-white.png" mode="" style="width: 11rpx;height: 20rpx;"></image>男神
												</view> -->
									</view>
								</view>
								<view class="right-user-address font28 colorfff">
									21岁 | 双鱼座 | 郑州
								</view>
							</view>
							<view class="flex right-user-bottom row-center">
								<view class="flex row-center font24 colorfff">
									<image src="../../static/image/place-white.png" mode=""></image>5km
								</view>
								<view class="flex row-center font24 colorfff">
									<text></text>当前在线
								</view>
							</view>
							<view class="flex area-between">

							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: "left-right",
		data() {
			return {
				startX: 0, //滑动开始x轴位置
				startY: 0, //滑动开始y轴位置
				moveX: 0, //滑动的x轴距离
				moveY: 0, //滑动的y轴距离
				like_state: 0, //-1：左滑，0：没滑动，1：右滑
				currentIndex: -1, //当前滑动的卡片index
				skewDeg: 0, //当前滑动卡片的倾斜度
				swiper_data: [{
						img: "../../static/image/1.jpg",
						anim: null,
						like_state: 0
					},
					{
						img: "../../static/image/swiper.png",
						anim: null,
						like_state: 0
					},
					{
						img: "../../static/image/1.jpg",
						anim: null,
						like_state: 0
					},
					{
						img: "../../static/image/swiper.png",
						anim: null,
						like_state: 0
					},
					{
						img: "../../static/image/1.jpg",
						anim: null,
						like_state: 0
					}
				]
			};
		},
		methods: {
			/**
			 * 触摸开始：记录位置
			 * */
			touchStart(event) {
				this.startX = event.touches[0].pageX;
				this.startY = event.touches[0].pageY;
			},
			/**
			 * 触摸开始滑动
			 * */
			touchMove(event) {
				var currentX = event.touches[0].pageX;
				var currentY = event.touches[0].pageY;
				var moveX = currentX - this.startX;
				var moveY = currentY - this.startY;
				var text = '';
				var currentIndex = event.currentTarget.dataset.id;
				var state = 0; //-1：左滑，0：没滑动，1：右滑
				// //左右方向滑动
				if (Math.abs(moveX) > Math.abs(moveY)) {
					if (moveX < -10) {
						text = '左滑';
						state = -1;
					} else if (moveX > 10) {
						text = '右滑';
						state = 1;
					}
					this.skew(currentIndex, moveX, moveY);
				} else { //上下方向滑动
					if (moveY < 0) text = '上滑';
					else if (moveY > 0) text = '下滑';
				}
				this.swiper_data[currentIndex]['text'] = text;
				this.swiper_data[currentIndex]['like_state'] = state;
				this.like_state = state;
				this.currentIndex = currentIndex;
				this.moveX = moveX;
				this.moveY = moveY;
			},
			/**
			 * 触摸结束
			 * */
			touchEnd(event) {
				// console.log(`移动距离:${Math.abs(this.moveX)}`)
				if (Math.abs(this.moveX) > 60) {
					this.hidden();
				} else {
					var anim = uni.createAnimation({
						duration: 300,
						timingFunction: 'ease-in-out'
					});
					anim.rotate(0)
						.step(1000);
					this.swiper_data[this.currentIndex]['anim'] = anim.export();
					this.swiper_data[this.currentIndex]['like_state'] = 0;
				}
			},
			/**
			 * 触摸卡片倾斜
			 * */
			skew(index, moveX, moveY) {
				let that = this;
				var anim = uni.createAnimation({
					duration: 500,
					timingFunction: 'linear'
				});
				this.swiper_data[index]['anim'] = anim;
				if (Math.abs(moveX) < 180) {
					this.skewDeg
					//console.log(`角度：${this.skewDeg}`)
					this.skewDeg = moveX / 4;
					anim.rotate(this.skewDeg)
						.step(1000);
					this.swiper_data[index]['anim'] = anim.export();
				}
			},
			/**
			 * 触摸结束，卡片消失
			 * */
			hidden() {
				var anim = uni.createAnimation({
					duration: 400,
					timingFunction: 'ease-in-out'
				});
				if (this.like_state == -1) {
					anim.translate(-400, -400)
				} else if (this.like_state == 1) {
					anim.translate(400, -400)
				}
				// console.log(`角度：${this.skewDeg}`)
				anim.rotate(this.skewDeg)
					.opacity(0)
					.step(1000);
				this.swiper_data[this.currentIndex]['anim'] = anim.export();
			}
		}
	}
</script>

<style lang="scss" scoped>
