		position: absolute;
		top: 20px;

	}

	.swiper_item_type image {
		width: 22px;
		height: 22px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}

	.swiper_item_type_like {
		left: 20px;
	}

	.swiper_item_type_dislike {
		right: 20px
	}
</style>
